/* 區塊命名 */
.section {
  &__banner {
    margin-top: 58px;
    height: 320px;
    background-image: url("../images/index-banner.jpg");
  }
  &__search {
    // transform: translateY 會影響 BS元件圖層 ?!
    // transform: translateY(-25%);
    // @include media-breakpoint-up(lg) {
    //   transform: translateY(-50%);
    // }
    margin-top: -52px;
    // margin-bottom: 66px;
  }
  &__topChoices {
    .inner {
      &__img {
        width: 255px;
        height: 164px;
        @include media-breakpoint-up(md) {
          height: 185px;
        }
        @include media-breakpoint-up(lg) {
          height: 175px;
        }
      }
    }
  }
  &__destinations {
    .inner {
      &__img {
        position: relative;
        &::after {
          position: absolute;
          content: "";
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          // .btn 與 圖片都需要 border-radius
          border-radius: 8px;
          background: linear-gradient(270deg, #00000000 0%, #000000 100%);
        }
        // TODO:圖片或許可以獨立出來使用
        &__Osaka {
          background-image: url("../images/Osaka.jpg");
        }
        &__Kyoto {
          background-image: url("../images/Kyoto.jpg");
        }
        &__Reykjavic {
          background-image: url("../images/Reykjavic.jpg");
        }
        &__Paris {
          background-image: url("../images/Paris.jpg");
        }
        &__Roma {
          background-image: url("../images/Roman.jpg");
        }
        &__Bangkok {
          background-image: url("../images/Bangkok.jpg");
        }
      }
      &__text {
        z-index: 9;
      }
    }
    .btn {
      &:focus {
        box-shadow: none;
      }
    }
  }
  &__inspiration {
    .inner {
      &__img {
        object-fit: cover;
        width: 240px;
        height: 181px;
        @include media-breakpoint-up(md) {
          width: 210px;
          height: 158px;
        }
        @include media-breakpoint-up(lg) {
          width: 350px;
          height: 263px;
        }
      }
    }
  }
  &__subscribe {
    @include media-breakpoint-up(md) {
      height: 320px;
    }
  }
}
