.logo {
  @include hidden-text(96px, 24px, url("../images/ic-logo-aloha.svg"));
}


.login {
  &__navImage {
    position: relative;
    &::before {
      position: absolute;
      content: "";
      width: 24px;
      height: 24px;
      background-image: url("../images/sung-wang-g4DgCF90EM4-unsplash.jpg");
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      border-radius: 50%;
      border: 2px solid map-get($theme-colors, primary);
      top: 0px;
      left: -32px;
      @include media-breakpoint-up(md){
        top: 0;
      }
    }
  }
  &__memberImage {
    width: 64px;
    height: 64px;
  }
}


.icon{
  &__rating{
    font-size: 18px;
  }
}


.section {
  &__login {
    position: relative;
    &__banner {
      position: absolute;
      background-image: url("../images/login.jpg");
      // min-height: 800px;
      height: 100vh;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
    &__info {
      position: relative;
      @include media-breakpoint-up(md){
        margin-top: 96px;
      }
      @include media-breakpoint-up(lg) {
        margin-top: 176px;
      }
      &__location {
        position: absolute;
        bottom: -20%;
        left: 50%;
        transform: translateX(-50%);
        @include media-breakpoint-up(lg) {
          bottom: -40%;
          left: 0;
          transform: translateX(0);
        }
      }
    }
  }
  &__searchResult {
    margin-top: 72px;
    .lineSeparator {
      &::before {
        @include media-breakpoint-down(md) {
          background-color: white;
        }
      }
    }
  }
}
.product {
  &__img {
    width: 150px;
    height: 150px;
    border-radius: 0;
    @include media-breakpoint-up(sm) {
      border-radius: 8px 0px 0px 8px;
    }
    @include media-breakpoint-up(lg) {
      width: 255px;
      height: 175px;
    }
    &__booking {
      @include media-breakpoint-down(sm) {
        width: 100%;
        height: 226px;
        border-radius: 8px 8px 0px 0px;
      }
    }
  }
  &__price {
    position: relative;
    font-size: 16px;
    &__title {
      @include media-breakpoint-up(md) {
        font-size: 24px;
        &::before {
          position: absolute;
          content: "per night";
          font-size: 12px;
          font-weight: normal;
          color: #00000099;
          top: -15px;
          right: 0;
        }
      }
    }
    &.active {
      .product__price__title {
        color: map-get($theme-colors, primary);
        &::before {
          top: -20px;
        }
      }
    }
    &__origin {
      font-size: 12px;
      @include media-breakpoint-up(md) {
        font-size: 14px;
      }
    }
  }
}

.list-group-item {
  position: relative;
  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background: #f1f0f8;
  }
}
.list-group-item.active {
  background-color: white;
  color: map-get($theme-colors, primary);
  transition: all 0.35s;
  &::after {
    background: map-get($theme-colors, primary);
  }
}
